
.add-present {
  background-color: $primary-color;
  color: #ffffff;

  @include middle-text;
  text-transform: none;
  font-family: $font-secondary;
  padding-top: 190px;
  padding-left: 65px;
  padding-right: 66px;
  padding-bottom: 40px;
  text-align: center;
  background-image: url("../img/gift-photo-mobile@1x.jpg");
  background-repeat: no-repeat;
  position: relative;
  margin-bottom: 35px;

  @media (min-resolution: $retina-dppx) {
    background-image: url("../img/gift-photo-mobile@2x.jpg");
  }

  @media (min-width: $tablet-width) {
    background-image: url("../img/gift-photo-tablet@1x.jpg");
    padding-left: 60%;
    padding-right: 130px;
    padding-bottom: 64px;
    padding-top: 77px;
    text-align: left;
    margin-bottom: 74px;

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/gift-photo-tablet@2x.jpg");
    }
  }

  @media (min-width: $desktop-width) {
    background-image: url("../img/gift-photo-desktop@1x.jpg");
    width: 20%;
    padding-left: 45px;
    padding-right: 40px;
    padding-top: 186px;
    padding-bottom: 43px;
    margin-bottom: auto;
    margin-top: 0;
    text-align: center;

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/gift-photo-desktop@2x.jpg");
    }
  }
}

.add-present::before {
  content: "";
  position: absolute;
  top: 65px;
  left: 50%;
  transform: translateX(-50%);
  background-image: url("../img/icon-gift.svg");
  background-repeat: no-repeat;
  width: 56px;
  height: 61px;

  @media (min-width: $tablet-width) {
    left: 120px;
  }

  @media (min-width: $desktop-width) {
    left: 50%;
    transform: translateX(-50%);
  }
}
