.fieldsets-wrapper {
  margin-bottom: 44px;

  @media (min-width: $tablet-width) {
    @include form-container;
    margin-bottom: 99px;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    justify-content: space-between;
    padding-right: 0;
    padding-left: 3px;
    margin-bottom: 10px;
    padding-top: 7px;
    padding-bottom: 45px;
  }
}
