.example {
  background-color: $grey-background;

  @media (min-width: $tablet-width) {
    border-top: $primary-border;
    background-color: #ffffff;
    background-image: linear-gradient(to bottom, transparent 57%, #eaeaea 57%);
  }

  @media (min-width: $desktop-width) {
    border-top: none;
    background-image: linear-gradient(to left, #eaeaea 26%, #f2f2f2 26%);
  }
}

.example__title {
  font-family: $font-primary;
  font-size: 36px;
  line-height: 60px;
  margin-bottom: 25px;
  margin-top: 0;
  font-weight: 400;

  @media (min-width: $tablet-width) {
    font-size: 60px;
    line-height: 60px;
    margin-bottom: 65px;
  }

  @media (min-width: $desktop-width) {
    margin-top: -174px;
    width: 40%;
    margin-bottom: 146px;
  }
}

.example__text {
  @include little-text;
  margin-top: auto;
  margin-bottom: 25px;
  width: 270px;

  @media (min-width: $tablet-width) {
    text-align: left;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 53px;
    width: 98%;
  }

  @media (min-width: $desktop-width) {
    width: 35%;
    margin-bottom: 69px;
  }
}

.example__list {
  @include reset-list;
  display: flex;
  justify-content: space-between;

  @media (min-width: $tablet-width) {
    width: 366px;
  }
}

.example__cost {
  text-transform: uppercase;
  text-align: center;
  font-family: $font-primary;
  font-size: 14px;
  line-height: 20px;
  margin-top: 33px;
  margin-bottom: 37px;

  @media (min-width: $tablet-width) {
    width: 179px;
    font-size: 20px;
    line-height: 31px;
    text-align: right;
    padding-right: 2px;
    padding-bottom: 10px;
    margin-top: 5px;
    margin-bottom: auto;
  }

  @media (min-width: $desktop-width) {
    width: 370px;
    font-size: 20px;
    text-align: left;
    margin-top: 54px;
  }
}

.example__cost-value {
  @media (min-width: $tablet-width) {
    margin-right: 3px;
  }

  @media (min-width: $desktop-width) {
    margin-left: 60px;
  }
}

.example__item {
  border: 1px solid #cdcdcd;
  padding-top: 13px;
  padding-left: 5px;
  padding-right: 5px;
  width: 124px;
  text-align: center;

  @media (min-width: $tablet-width) {
    width: 162px;
  }
}

.example__item-title {
  text-transform: uppercase;
  font-size: 24px;
  line-height: 24px;
  font-family: $font-primary;
  margin: 0;
  margin-bottom: 8px;
  font-weight: 400;

  @media (min-width: $tablet-width) {
    font-size: 30px;
    line-height: 37px;
    margin-bottom: 11px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 12px;
  }
}

.example__item-info {
  font-size: 12px;
  line-height: 12px;
  font-family: $font-secondary;
  color: $text-color;
  padding-left: 17px;
  padding-right: 16px;
  text-align: center;
  background-color: $grey-background;
  margin-bottom: -15px;
  margin-top: 0;

  @media (min-width: $tablet-width) {
    font-size: 14px;
    line-height: 14px;
    padding-left: 5px;
    padding-right: 5px;
    background-color: #ffffff;
    margin-bottom: -5px;
  }

  @media (min-width: $desktop-width) {
    background-color: $grey-background;
  }
}

.example__picture {
  margin-bottom: 55px;
  height: auto;
  width: 100%;

  @media (min-width: $tablet-width) {
    margin-top: 63px;
    width: 100%;
    margin-bottom: 14px;
  }

  @media (min-width: $desktop-width) {
    width: 50%;
    text-align: right;
    margin-left: 50%;
    margin-bottom: 160px;
    margin-top: -600px;
  }
}

.example__button {
  @include middle-text;
  font-family: $font-primary;
  background-color: transparent;
  padding-left: 0;
  padding-right: 0;
  outline: none;
  border: none;
  color: #111111;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    padding-bottom: 7px;
    border-bottom: 1px dotted #cdcdcd;
  }
}

.example__button:hover {
  @media (min-width: $tablet-width) {
    border-bottom: 1px solid #cdcdcd;
  }
}

.example__button:active {
  @media (min-width: $tablet-width) {
    border-bottom: none;
  }
}

.example__line {
  width: 84px;
  height: 14px;
  background-color: #ffffff;
  border: $primary-border;
  border-radius: 20%;
  margin-right: 19px;
  margin-left: 18px;
  position: relative;
  padding-left: 5px;

  @media (min-width: $tablet-width) {
    width: 428px;
    background-color: #dcdcdc;
    height: 6px;
    border: none;
    margin-left: 25px;
  }
}

.example__line-center {
  background-color: $primary-color;
  width: 35px;
  height: 6px;
  position: absolute;
  top: 3px;
  outline: none;

  @media (min-width: $tablet-width) {
    width: 34px;
    height: 34px;
    background-color: white;
    border-radius: 50%;
    padding: 0;
    top: -17px;
    left: 16px;
  }

  @media (min-width: $tablet-width) {
    left: 46%;
  }
}

.example__line-center:hover {
  box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.25);
}

.example__line-center:active {
  border: 1px solid #68b738;
  box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.3);
}

.example__line-center::before {
  @media (min-width: $tablet-width) {
    content: "";
    position: absolute;
    width: 8px;
    height: 8px;
    background-color: $primary-color;
    border-radius: 50%;
    top: 38%;
    left: 38%;
  }
}

.example__list-wrapper {
  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    margin-bottom: 5px;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    flex-direction: column;
    width: auto;
  }
}
