.form-text {
  @include little-text;
  color: #111111;
  text-transform: uppercase;
  font-family: $font-primary;

  @include form-container;
  margin-top: 0;
  text-align: left;
  padding-right: 40px;
  margin-bottom: 33px;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 30px;
    padding-right: 370px;
    margin-bottom: 60px;
  }

  @media (min-width: $desktop-width) {
    padding-right: 890px;
    margin-bottom: 60px;
  }
}
