.form-title {
  margin-top: 0;
  font-size: 36px;
  padding-top: 15px;
  line-height: 60px;
  margin-bottom: 22px;
  font-family: $font-primary;
  border-top: $primary-border;
  font-weight: 400;

  @include container;
  text-transform: none;

  @media (min-width: $tablet-width) {
    font-size: 60px;
    line-height: 60px;
    margin-bottom: 55px;
    margin-top: 43px;
    border-top: none;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 54px;
    margin-top: 59px;
  }
}
