.catalog__list {
  @include reset-list;

  @media (width: $mobile-width-only) {
    padding-left: 0;
    padding-right: 0;
  }

  @media (min-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 21px;
  }

  @media (min-width: $desktop-width) {
    justify-content: flex-start;
    padding-bottom: 28px;

    @include container;
  }
}

.catalog__line {
  height: 3px;
  background-color: $primary-color;
}
