.form-block {
  border: none;
}

.form-block__item {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.form-block__label-text {
  margin-right: 15px;
}

.form-block__input-text {
  border: $primary-border;
  flex-grow: 1;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-right: 16px;
  padding-left: 15px;
  margin-bottom: auto;
  outline: none;
  box-shadow: none;
  background-color: #ffffff;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  font-family: $font-primary;
  color: $text-color;
  text-align: left;
  height: 45px;

  @media (min-width: $tablet-width) {
    padding-top: 17px;
    padding-bottom: 19px;
    padding-left: 19px;
    font-size: 19px;
    line-height: 19px;
    height: 52px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 14px;
    padding-bottom: 17px;
    padding-left: 18px;
    margin-bottom: 0;
    margin-right: 5px;
  }
}

.form-block__input-text:hover {
  border: $primary-border;
}

.form-block__input-text:focus {
  border: 1px solid #444444;
}

.form-block__item--facts {
  @media (min-width: $tablet-width) {
    margin-bottom: 19px;
  }
}

.form-block__label-text--name {
  margin-right: 12px;

  @media (min-width: $tablet-width) {
    margin-right: 17px;
  }

  @media (min-width: $desktop-width) {
    margin-right: 15px;
  }
}

.form-block__label-text--weight {
  @media (min-width: $tablet-width) {
    margin-right: 18px;
  }
}

.form-block__input-text::placeholder {
  @include form-placeholder;
}

.form-block__input-text::-moz-placeholder {
  color: #444444;
  opacity: 1;
}

.form-block__input-text::-webkit-input-placeholder {
  color: #444444;
  opacity: 1;
}

.form-block__label-radio {
  padding-left: 46px;
  position: relative;

  @media (min-width: $tablet-width) {
    padding-left: 60px;
  }
}

.form-block__label-radio:active {
  opacity: 30%;
}

.form-block__label-radio::before {
  content: "";
  width: 30px;
  height: 30px;
  border: $primary-border;
  top: -5px;
  left: 0;
  position: absolute;
  border-radius: 50%;

  @media (min-width: $tablet-width) {
    width: 40px;
    height: 40px;
    bottom: 10px;
    left: 0;
    top: -10px;
  }
}

.form-block__label-radio::before:hover {
  border: 1px solid #bababa;
}

.form-block__label-radio::before:active {
  opacity: 30%;
}

.form-block__label-radio::after {
  content: "";
  width: 12px;
  height: 12px;
  background-color: $primary-color;
  left: 9px;
  top: 4px;
  position: absolute;
  border-radius: 50%;
  display: none;

  @media (min-width: $tablet-width) {
    width: 18px;
    height: 18px;
    left: 11px;
    top: 1px;
  }

  @media (min-width: $desktop-width) {
    left: 11px;
    top: 1px;
  }
}

.form-block__label-checkbox {
  padding-left: 45px;
  position: relative;

  @media (min-width: $tablet-width) {
    padding-left: 56px;
  }
}

.form-block__label-checkbox:active {
  opacity: 30%;
}

.form-block__label-checkbox::before {
  content: "";
  width: 30px;
  height: 30px;
  border: $primary-border;
  top: -3px;
  left: 0;
  position: absolute;

  @media (min-width: $tablet-width) {
    width: 40px;
    height: 40px;
  }
}

.form-block__label-checkbox::before:hover {
  border: 1px solid #bababa;
}

.form-block__label-checkbox::before:active {
  opacity: 30%;
}

.form-block__label-checkbox::after {
  content: "";
  position: absolute;
  top: 4px;
  left: 6px;
  width: 22px;
  height: 17px;
  background-image: url("../img/icon-tick.svg");
  background-repeat: no-repeat;
  display: none;

  @media (min-width: $tablet-width) {
    top: 7px;
    left: 11px;
  }
}

.form-block__label-checkbox::after:active {
  opacity: 30%;
}

.form-block__input-checkbox:checked + .form-block__label-checkbox::after {
  display: block;
}

.form-block__input-checkbox:checked::after:active {
  opacity: 30%;
}

.form-block__input-radio:checked + .form-block__label-radio::after {
  display: block;
}

.form-block--facts {
  @include form-container;
  padding-bottom: 6px;

  @media (min-width: $tablet-width) {
    margin-bottom: 40px;
    padding-left: 0;
    padding-right: 0;
    width: 100%;
    padding-bottom: 15px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 4px;
    width: 44%;
    padding-top: 0;
  }
}

.form-block--goal-container {
  @media (min-width: $tablet-width) {
    padding-left: 0;
    padding-right: 0;
    width: 100%;
  }
}

.form-block--goal {
  @include form-container;
  padding-top: 25px;
  padding-bottom: 0;
  border: $primary-border;
  margin-bottom: 28px;
  overflow: hidden;

  @media (min-width: $tablet-width) {
    padding-left: 27px;
    width: 100%;
    height: 196px;
    padding-top: 33px;
    margin-bottom: 79px;
    border: $primary-border;
  }

  @media (min-width: $desktop-width) {
    width: 44%;
    height: 196px;
    padding-top: 33px;
    margin-bottom: auto;
    padding-left: 27px;
  }
}

.form-block__item--goal {
  margin-bottom: 25px;

  @media (min-width: $tablet-width) {
    margin-bottom: 33px;
  }
}

.form-block__title {
  background-color: #ffffff;
  padding-left: 37px;
  padding-right: 30px;
  margin-left: -38px;
  text-transform: none;
  font-size: 24px;
  line-height: 24px;
  font-family: $font-primary;
  margin-bottom: 28px;
  font-weight: 400;

  @media (min-width: $tablet-width) {
    font-size: 36px;
    line-height: 36px;
    text-align: left;
    margin-bottom: 68px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 10px;
  }
}

.form-block--contact {
  margin-bottom: 17px;
  margin-top: -20px;

  @include form-container;

  @media (min-width: $tablet-width) {
    padding-bottom: 55px;
    margin-top: -33px;
  }

  @media (min-width: $desktop-width) {
    margin-top: -44px;
    padding-bottom: 54px;
  }
}

.form-block__contact-wrapper {
  @media (min-width: $desktop-width) {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
  }
}

.form-block__title--contact {
  width: 249px;
  margin-bottom: 30px;

  @media (min-width: $tablet-width) {
    width: 590px;
    margin-bottom: 76px;
  }
}

.form-block__item--email {
  position: relative;

  @media (min-width: $tablet-width) {
    margin-bottom: 19px;
  }

  @media (min-width: $desktop-width) {
    width: 47%;
    margin-right: 21px;
  }
}

.form-block__label-text--tel {
  @media (min-width: $desktop-width) {
    margin-right: 20px;
  }
}

.form-block__input-text--contact {
  @media (min-width: $desktop-width) {
    margin-right: auto;
  }
}

.form-block__item--email::after {
  content: "";
  width: 18px;
  height: 15px;
  position: absolute;
  right: 15px;
  top: 17px;
  background-image: url("../img/icon-mail.svg");
  background-repeat: no-repeat;

  @media (min-width: $tablet-width) {
    top: 20px;
    right: 22px;
  }

  @media (min-width: $desktop-width) {
    top: 20px;
    right: 20px;
  }
}

.form-block__item--tel {
  position: relative;

  @media (min-width: $desktop-width) {
    width: 47%;
  }
}

.form-block__item--tel::after {
  content: "";
  width: 21px;
  height: 21px;
  position: absolute;
  right: 13px;
  top: 13px;
  background-image: url("../img/icon-phone.svg");
  background-repeat: no-repeat;

  @media (min-width: $tablet-width) {
    top: 17px;
    right: 17px;
  }

  @media (min-width: $desktop-width) {
    top: 17px;
    right: 17px;
  }
}

.form-block__label-text--email {
  margin-right: 10px;

  @media (min-width: $tablet-width) {
    margin-right: 22px;
  }

  @media (min-width: $desktop-width) {
    margin-right: 21px;
  }
}

.form-block--adress {
  margin-top: 30px;
  border-top: 3px solid #68b738;

  @media (min-width: $tablet-width) {
    padding-bottom: 70px;
  }
}

.form-block__title--adress {
  background-color: #ffffff;
  text-transform: none;
  padding-right: 0;
  width: 250px;
  font-size: 24px;
  line-height: 24px;
}

.form-block--comment {
  margin-bottom: 31px;
  margin-top: -17px;

  @include form-container;

  @media (min-width: $tablet-width) {
    margin-top: -25px;
  }

  @media (min-width: $desktop-width) {
    margin-top: -25px;
    padding-bottom: 15px;
  }
}

.form-block__input-textarea {
  width: 100%;
  padding-top: 13px;
  padding-left: 15px;
  padding-right: 11px;
  height: 150px;
  border: $primary-border;
  outline: none;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  font-family: $font-primary;
  color: $text-color;
  text-align: left;
  overflow: auto;
  resize: none;

  @media (min-width: $tablet-width) {
    margin-bottom: 42px;
    padding-top: 10px;
    padding-left: 20px;
    height: 176px;
    font-size: 19px;
    line-height: 19px;
  }
}

.form-block__input-textarea:focus {
  padding-top: 13px;
  padding-left: 15px;
  padding-right: 11px;
  border: 1px solid #444444;

  @media (min-width: $tablet-width) {
    padding-top: 10px;
  }
}

.form-block__input-textarea::placeholder {
  @include form-placeholder;

  @media (min-width: $tablet-width) {
    line-height: 30px;
  }
}

.form-block__input-textarea::-webkit-input-placeholder {
  color: #444444;
  opacity: 1;
}

.form-block__input-textarea::-moz-placeholder {
  color: #444444;
  opacity: 1;
}

.form-block__input-textarea:-moz-placeholder {
  color: #444444;
  opacity: 1;
}

.form-block__input-textarea:-ms-input-placeholder {
  color: #444444;
  opacity: 1;
}

.form-block__add-item {
  margin-top: 7px;
  margin-bottom: 25px;

  @media (min-width: $tablet-width) {
    width: 45%;
    margin-bottom: 47px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 10px;
    width: 20%;
    margin-bottom: 54px;
  }
}

.form-block--add {
  @include form-container;
  margin-top: -17px;

  @media (min-width: $tablet-width) {
    margin-bottom: 5px;
    margin-top: -27px;
  }

  @media (min-width: $desktop-width) {
    margin-top: -27px;
  }
}

.form-block__title--add {
  @media (min-width: $tablet-width) {
    margin-bottom: 50px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 50px;
  }
}

.form-block__add-wrapper {
  @media (min-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    flex-direction: row;
  }
}
