.social {
  display: flex;
  justify-content: space-between;
  width: 160px;
  padding-left: 13px;
  padding-right: auto;
  align-items: center;

  @media (min-width: $tablet-width) {
    border-bottom: none;
    padding: 0;
    width: 125px;
  }
}

.social__link-svg {
  fill: #666666;
}

.social__link-svg:hover {
  fill: #68b738;
}

.social__link:active {
  opacity: 30%;
}

.social__link--insta-svg {
  height: 17px;
  width: 17px;

  @media (min-width: $tablet-width) {
    height: 23px;
    width: 23px;
  }
}

.social__link--fb-svg {
  height: 19px;
  width: 10px;

  @media (min-width: $tablet-width) {
    height: 28px;
    width: 14px;
  }
}

.social__link--vk-svg {
  height: 12px;
  width: 22px;

  @media (min-width: $tablet-width) {
    height: 16px;
    width: 30px;
  }
}
