.benefits {
  @include container;

  @media (min-width: $tablet-width) {
    font-size: 16px;
    line-height: 24px;
    padding-bottom: 0;
    margin-bottom: 0;
  }

  @media (min-width: $desktop-width) {
    font-size: 16px;
    line-height: 24px;
    padding-bottom: 125px;
    margin-bottom: auto;
  }
}

.benefits__list {
  @include reset-list;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
    counter-reset: list;
    justify-content: space-between;
    width: 90%;
  }

  @media (min-width: $desktop-width) {
    flex-wrap: nowrap;
    width: 100%;
  }
}

.benefits__title {
  font-family: $font-primary;
  font-size: 36px;
  line-height: 60px;
  text-align: left;
  margin-top: 17px;
  margin-bottom: 29px;
  font-weight: 400;

  @media (min-width: $tablet-width) {
    font-size: 60px;
    margin-bottom: 79px;
    margin-top: 43px;
    line-height: 50px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 80px;
    margin-top: 49px;
  }
}

.benefits__item {
  @include little-text;
  margin-bottom: 20px;
  padding-left: 80px;
  padding-right: 5px;
  padding-bottom: 6px;
  position: relative;

  @media (min-width: $tablet-width) {
    width: 40%;
    font-size: 16px;
    line-height: 24px;
    padding-left: 0;
    margin-bottom: 90px;
    position: relative;
    padding-top: 110px;
    padding-right: 5px;
    padding-bottom: 43px;
  }

  @media (min-width: $desktop-width) {
    padding-right: 40px;
    padding-bottom: 48px;
    margin-bottom: 0;
    width: 20%;
  }
}

.benefits__item::before {
  content: "";
  width: 60px;
  height: 60px;
  background-color: $primary-color;
  position: absolute;
  left: 0;
  background-repeat: no-repeat;
  background-position: center;

  @media (min-width: $tablet-width) {
    width: 80px;
    height: 80px;
    top: 0;
  }
}

.benefits__item::after {
  @media (min-width: $tablet-width) {
    counter-increment: list;
    content: counter(list) "";
    position: absolute;
    top: 90px;
    height: 228px;
    width: 121px;
    left: 110px;
    color: #f2f2f2;
    font-size: 281px;
    font-family: $font-primary;
    z-index: -1;
  }
}
