.add-title {
  font-size: 24px;
  line-height: 24px;
  font-family: $font-primary;
  text-align: left;
  padding-bottom: 35px;
  border-bottom: $primary-border;
  margin-top: 30px;
  margin-bottom: 0;
  font-weight: 400;

  @media (min-width: $tablet-width) {
    border-bottom: none;
    font-size: 36px;
    line-height: 36px;
    width: 58%;
    margin-top: -25px;
    margin-bottom: 45px;
    background-color: #ffffff;
    text-transform: none;
    font-family: $font-primary;
    font-weight: 400;
    padding-left: 35px;
    padding-right: 20px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 45px;
    margin-top: -24px;
    width: 34%;
    margin-left: -36px;
    padding-left: 34px;
  }
}
