.choice__list {
  @include reset-list;
  @include container;
  margin-top: -10px;

  @media (min-width: $tablet-width) {
    margin-bottom: 51px;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    flex-wrap: nowrap;
    margin-bottom: 45px;
  }
}

.choice__item {
  @media (min-width: $desktop-width) {
    margin-bottom: 20px;
  }
}
